import React from "react";

function ReadOnlyRow(item, handleEditClick) {
  return (
    <tr>
      <td>{item.headtype}</td>
      <td>{item.norecieved}</td>
      <td>{item.compsize}</td>
      <td>{item.storeslocation}</td>
      <td>{item.damagecheck}</td>
      <td>
        <button type="button" onClick={(event) => handleEditClick(event, item)}>
          Edit
        </button>
      </td>
    </tr>
  );
}

export default ReadOnlyRow;
